                    val.ANSWER = val.REFERENCE_ANSWER.split('|');
                    val.DISABLED = true;
                  }
                  else {
                    val.ANSWER = [];
                    val.reference_answer = val.REFERENCE_ANSWER.split('|');
                    val.DISABLED = false;
                  }
                }
                val.sign = false;
              });
