        console.log(arg)
        if (type) {
          if (arg.ANSWER.length > 1) {
            arg.ANSWER = Array.prototype.join.call(arg.ANSWER, '|');
          }
          else if (arg.ANSWER.length == 0) {
            arg.ANSWER = '';
          }
          else {
            arg.ANSWER = Array.prototype.join.call(arg.ANSWER, '');
          }
        }
        console.log(arg.ANSWER)
        let requestObj = {
          questions_id: arg.ID,
          pitch_on_option: arg.ANSWER,
          answer_results: arg.is_true,
          VALUE: arg.VALUE,
          user_id: this.user_id,
          user_name: this.user_name,
          // user_name: "",
        }
        console.log(requestObj)
        request.getServerData(
          requestObj,
          "onlineExamination.action.examinationAction[markingPapers]",
          true,
          (result) => {
            console.log(result)
            // this.wait = false;
            if (result == '') {
              mui.alert('', '', '', self.back, 'div')
            }
            else {
              var res = JSON.stringify(result);
              if (arg.QUESTIONS_TYPE == '1') {//
                arg.ANSWER = arg.ANSWER.split('|');
              }
              if (callback) {
                callback();
              }
              // this.$router.push({
              //   path: 'answerResult',
              //   query: {res}
              // })
            }

            // this.$router.go(-1);
            // var res = result;
            // localStorage.removeItem([this.classid]);
            // res.state = this.state;
            // var resStr = JSON.stringify(res)
            // this.$router.push({
            //   path: 'answerResult',
            //   query: {resStr}
            // });
          })

