        else {//
          if (JSON.stringify(Array.prototype.sort.call(this.examlist[this.page - 1].ANSWER)) == JSON.stringify(Array.prototype.sort.call(this.examlist[this.page - 1].reference_answer))) {//
            let callback = () => {
              this.examlist[this.page - 1].is_true = '0';
